  margin-left: 10px;
}

.card-title span.add-margin-link {
  opacity: 0;
  -webkit-transition: opacity .25s ease-in-out;
  transition: opacity .25s ease-in-out;
}

.card-title:hover span.add-margin-link {
  opacity: 1;
}



